// src/App.js
import React, { useState, useEffect, useCallback } from "react";
import "./index.css";
import background from "./assets/background.jpg";
import CI from "./assets/matchup-CI.png";

import { ModeProvider, useMode } from "./ModeContext";
import ModeSelector from "./components/ModeSelector";
import StartScreen from "./components/StartScreen";
import TeamSelectScreen from "./components/TeamSelectScreen";
import StadiumScanScreen from "./components/StadiumScanScreen";
import CountdownScreen from "./components/CountdownScreen";
import GameScreen from "./components/GameScreen";
import ResultScreen from "./components/ResultScreen";

import {
  submitScore,
  subscribeStadiumsList,
} from "./firebase";

function AppContent() {
  const { mode, setMode, teamId, stadiumId, setStadiumId } = useMode();
  const [screen, setScreen] = useState("select");
  const [name, setName] = useState("");
  const [count, setCount] = useState(3);
  const [score, setScore] = useState(0);

  // **      **
  const [stadiumList, setStadiumList] = useState([]);
  useEffect(() => {
    const unsub = subscribeStadiumsList((list) => {
      // list = [{ id: "hanbat", name: "Hanbat" }, ...]
      setStadiumList(list.map((s) => s.id)); // ID  (: ["hanbat", "seoul", ...])
    });
    return () => {
      if (unsub) unsub();
    };
  }, []);

  // 1)     
  const handleModeNext = useCallback(
    (m) => {
      setMode(m);
      if (m === "personal") {
        setScreen("start");
      } else if (m === "team") {
        setScreen("teamSelect");
      } else if (m === "stadium") {
        //    QR   
        setScreen("stadiumScan");
      }
    },
    [setMode]
  );

  // 2)  
  const handleNameStart = useCallback((userName) => {
    setName(userName);
    setScore(0);
    setCount(3);
    setScreen("countdown");
  }, []);

  // 3)  
  const handleTeamStart = useCallback(() => {
    setScore(0);
    setCount(3);
    setScreen("countdown");
  }, []);

  // 4)                
  const handleStadiumStart = useCallback(
    (decodedText) => {
      // QR  "Stadium-{ID}"  
      const prefix = "Stadium-";
      if (!decodedText.startsWith(prefix)) {
        alert("  QR .");
        return;
      }
      // "Stadium-hanbat" -> "hanbat"
      const plainId = decodedText.slice(prefix.length).trim();
      if (!plainId) {
        alert(" ID  .");
        return;
      }
      // **   ID  **
      if (!stadiumList.includes(plainId)) {
        alert(`"${plainId}"    .`);
        return;
      }
      //   ID Context    
      setStadiumId(plainId);
      setScreen("stadiumName");
    },
    [stadiumList, setStadiumId]
  );

  // 5)        
  const handleStadiumNameStart = useCallback((userName) => {
    setName(userName);
    setScore(0);
    setCount(3);
    setScreen("countdown");
  }, []);

  // 6)        finish 
  const handleFinish = useCallback(() => {
    if (score > 0) {
      submitScore({
        mode,
        name: mode === "personal" || mode === "stadium" ? name : "",
        score,
        teamId: mode === "team" ? teamId : null,
        stadiumId: mode === "stadium" ? stadiumId : null,
      });
    }
    setScreen("finish");
  }, [mode, name, score, teamId, stadiumId]);

  // 7)     
  const handleGameClick = useCallback(() => {
    if (screen === "game") {
      setScore((s) => s + 1);
    }
  }, [screen]);

  // 8) 
  const handleRetry = useCallback(() => {
    setName("");
    setScore(0);
    setStadiumId(null);
    setScreen("select");
  }, [setStadiumId]);

  // 9)  
  useEffect(() => {
    if (screen === "countdown") {
      if (count > 0) {
        const t = setTimeout(() => setCount((c) => c - 1), 1000);
        return () => clearTimeout(t);
      }
      setScreen("game");
    }
  }, [screen, count]);

  // 10) 'finish'  1.5  'result'
  useEffect(() => {
    if (screen === "finish") {
      const t = setTimeout(() => setScreen("result"), 1500);
      return () => clearTimeout(t);
    }
  }, [screen]);

  //    
  if (screen === "select") {
    return (
      <div className="flex justify-center items-center w-screen h-screen bg-black overflow-hidden">
        <div
          className="relative w-full max-w-[375px] h-full flex items-center justify-center p-4"
          style={{
            backgroundImage: `url(${background})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        >
          <div className="absolute inset-0 bg-black opacity-80" />
          <div className="relative z-10 w-full">
            <ModeSelector onNext={handleModeNext} />
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="flex justify-center items-center w-screen h-screen bg-black overflow-hidden">
      {/*    */}
      {screen === "finish" && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-90 animate-fade-in">
          <span className="text-6xl font-bold text-white"> !</span>
        </div>
      )}

      {/*    CI  */}
      <div
        className="relative w-full max-w-[375px] h-full flex items-start md:items-center justify-center p-4"
        style={{
          backgroundImage: `url(${background})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
        }}
      >
        {/*  CI  ( ) */}
        {screen !== "result" && (
          <img
            src={CI}
            alt="MatchUp CI"
            className="absolute top-4 left-1/2 transform -translate-x-1/2 w-20 z-10"
          />
        )}
        {/*   */}
        <div className="absolute inset-0 bg-black opacity-80" />

        <div className="relative z-20 w-full h-full p-4">
          {screen === "start" && <StartScreen onStart={handleNameStart} />}
          {screen === "teamSelect" && (
            <TeamSelectScreen onStartGame={handleTeamStart} />
          )}
          {screen === "stadiumScan" && (
            <StadiumScanScreen onScanSuccess={handleStadiumStart} />
          )}
          {screen === "stadiumName" && (
            <StartScreen onStart={handleStadiumNameStart} />
          )}
          {screen === "countdown" && <CountdownScreen count={count} />}
          {screen === "game" && (
            <GameScreen onClick={handleGameClick} onFinish={handleFinish} />
          )}
          {screen === "result" && (
            <ResultScreen name={name} score={score} onRetry={handleRetry} />
          )}
        </div>
      </div>
    </div>
  );
}

export default function App() {
  return (
    <ModeProvider>
      <AppContent />
    </ModeProvider>
  );
}
